import { observer } from "mobx-react";
import { NodeCollectionStore } from "../../stores/NodeCollectionStore";
import { FreeFormCanvas } from "../freeformcanvas/FreeFormCanvas";
import { TopBar } from "./TopBar";
import React = require("react");
import "./NodeView.scss";

export interface IProps {
    store: NodeCollectionStore;
}

export const nestedNodeCollection = new NodeCollectionStore();

@observer
export class CollectionNodeView extends React.Component<IProps> {

    onPointerDown = (e: React.PointerEvent): void => {
        e.stopPropagation();
    }


    render() {
        let store = this.props.store;
        return (
            <div className="node text-node" style={{ transform: store.Transform }}onPointerDown={this.onPointerDown} >
                <TopBar store={store}/>
                <div className="scroll-box">
                    <div className="content">
                        <FreeFormCanvas store={nestedNodeCollection} />                    
                    </div>
                </div>
            </div>
        );
    }
}