import { observer } from "mobx-react";
import { NodeCollectionStore } from "../../stores/NodeCollectionStore";
import { NodeContainer } from "./NodeContainer";
import { StaticTextNodeStore } from '../../stores/StaticTextNodeStore';
import { VideoNodeStore } from '../../stores/VideoNodeStore';
import { ImageNodeStore } from '../../stores/ImageNodeStore';
import { WebNodeStore } from '../../stores/WebNodeStore';
import { PDFNodeStore } from '../../stores/PDFNodeStore';
import { mainNodeCollection } from '../../Main';
import React = require("react");
import "./FreeFormCanvas.scss";

interface IProps {
    store: NodeCollectionStore
}

let nodes = []

@observer
export class FreeFormCanvas extends React.Component<IProps> {

    private _isPointerDown: boolean;

    onPointerDown = (e: React.PointerEvent): void => {
        e.stopPropagation();
        e.preventDefault();
        this._isPointerDown = true;
        document.removeEventListener("pointermove", this.onPointerMove);
        document.addEventListener("pointermove", this.onPointerMove);
        document.removeEventListener("pointerup", this.onPointerUp);
        document.addEventListener("pointerup", this.onPointerUp);
    }

    onPointerUp = (e: PointerEvent): void => {
        e.stopPropagation();
        e.preventDefault();
        this._isPointerDown = false;
        document.removeEventListener("pointermove", this.onPointerMove);
        document.removeEventListener("pointerup", this.onPointerUp);
    }

    onPointerMove = (e: PointerEvent): void => {
        e.stopPropagation();
        e.preventDefault();
        if (!this._isPointerDown) {
            return;
        }
        this.props.store.X += e.movementX;
        this.props.store.Y += e.movementY;
    }

    addTextNode (e: React.MouseEvent<HTMLInputElement>) {
        nodes.push(new StaticTextNodeStore({ X: Math.random() * 300, Y: Math.random() * 300, Title: "Text Node Title", Text: "Some text :))" }));
        mainNodeCollection.AddNodes(nodes);
        // this.props.store.AddNodes(nodes);
        nodes = [];
    }

    addVideoNode (e: React.MouseEvent<HTMLInputElement>) {
        nodes.push(new VideoNodeStore({ X: Math.random() * 300, Y: Math.random() * 300, Title: "Video Node Title", Url: "http://cs.brown.edu/people/peichman/downloads/cted.mp4" }));
        mainNodeCollection.AddNodes(nodes);
        nodes = [];
    }

    addImageNode (e: React.MouseEvent<HTMLInputElement>) {
        nodes.push(new ImageNodeStore({ X: Math.random() * 300, Y: Math.random() * 300, Title: "Image Node Title", Url: "https://cs.brown.edu/events/25th-anniversary/images/chairs/avd_2_now.jpg" }));        
        mainNodeCollection.AddNodes(nodes);
        nodes = [];
    }

    addWebNode (e: React.MouseEvent<HTMLInputElement>) {
        nodes.push(new WebNodeStore({ X: Math.random() * 300, Y: Math.random() * 300, Title: "Web Title", Url: "https://bbc.com" }));        
        mainNodeCollection.AddNodes(nodes);
        nodes = [];
    }

    addPDFNode (e: React.MouseEvent<HTMLInputElement>) {
        nodes.push(new PDFNodeStore({ X: Math.random() * 300, Y: Math.random() * 300, Title: "PDF Title", Url: "https://cs.brown.edu/courses/cs015/docs/CS15Missive2019.pdf" }));        
        mainNodeCollection.AddNodes(nodes);
        nodes = [];
    }

    addCollectionNode (e: React.MouseEvent<HTMLInputElement>) {
        nodes.push(new NodeCollectionStore());        
        mainNodeCollection.AddNodes(nodes);
        nodes = [];
    }

    render() {
        let store = this.props.store;
        return (
            <div className="freeformcanvas-container" onPointerDown={this.onPointerDown}>
                <div className="btn-group">
                    <button className ="textNodeButton" onClick={this.addTextNode}>Text</button>
                    <button className ="videoNodeButton" onClick={this.addVideoNode}>Video</button>
                    <button className ="imageNodeButton" onClick={this.addImageNode}>Image</button>
                    <button className ="webNodeButton" onClick={this.addWebNode}>Website</button>
                    <button className ="pdfNodeButton" onClick={this.addPDFNode}>PDF</button>
                    <button className ="collectionNodeButton" onClick={this.addCollectionNode}>Collection</button>
                </div>
                <div className="freeformcanvas" style={{transform: store.Transform }}>
                    <NodeContainer store={store} />
                </div>
            </div>
        );
    }
}
