import { observer } from "mobx-react";
import { PDFNodeStore } from "../../stores/PDFNodeStore";
import { TopBar } from "./TopBar";
import { Document, Page } from 'react-pdf';
import React = require("react");
import "./NodeView.scss";

interface IProps {
    store: PDFNodeStore;
}

@observer
export class PDFNodeView extends React.Component<IProps> {

    private _isPointerDown = false;

    state = {
        numPages: null,
        pageNumber: 1,
    }

    onPointerDown = (e: React.PointerEvent): void => {
        e.stopPropagation();
    }

    onDocumentLoadSuccess = ({ numPages }) => {
        this.setState({ numPages });
    }


    render() {
        const { pageNumber, numPages } = this.state;
        let store = this.props.store;
        return (
            <div className="node text-node" style={{ transform: store.Transform }}onPointerDown={this.onPointerDown} >
                <TopBar store={store} />
                <div className="scroll-box">
                    <div className="content">
                        <h3 className="title">{store.Title}</h3>
                        <Document file = {store.Url}
                        onLoadSuccess={this.onDocumentLoadSuccess}>
                            <Page pageNumber={pageNumber} />
                        </Document>
                    </div>
                </div>
            </div>
        );
    }
}