import { observer } from "mobx-react";
import { StaticTextNodeStore } from "../../stores/StaticTextNodeStore";
import { TopBar } from "./TopBar";
import React = require("react");
import ReactQuill, {Quill} from 'react-quill';
import "./NodeView.scss";
import 'react-quill/dist/quill.snow.css';


interface IProps {
    store: StaticTextNodeStore;
}

interface IState {
    text: string;
}

@observer
export class TextNodeView extends React.Component<IProps, IState> {

    constructor(props) {
        super(props)
        this.state = {text: '' } // You can also pass a Quill Delta here
        this.handleChange = this.handleChange.bind(this)
    }

    onPointerDown = (e: React.PointerEvent): void => {
        e.stopPropagation();
    }
    
    handleChange(value) {
    this.setState({text: value })
    }

    render() {
        let store = this.props.store;
        return (
            <div className="node text-node" style={{transform: store.Transform}} onPointerDown={this.onPointerDown} >
                <TopBar store={store} />
                <div className="scroll-box">
                    <div className="content">
                        <h3 className="title">{store.Title}</h3>
                        <ReactQuill className = "textEditor" value={this.state.text} onChange={this.handleChange}/>
                        <p className="paragraph">{store.Text}</p>
                    </div>
                </div>
            </div>
        );
    }
}